footer {
    height: 10vh;
    display: flex;    
    align-items: center;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

.copyright {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    padding-left: 1rem;
    width: 60%;
}

.social {
    float: right;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    width: 40%;
}

footer img {
    margin-left: .5rem;
    padding: 0.1rem;        
    border: 1px solid #FAF4D3;
    box-shadow: none;
    border-radius: 50%;
    width: 2rem;
    background: #fff;
}

@media (min-width: 700px) {
    .copyright {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        padding-left: 0rem;
        width: 60%;
    }

    footer img {
        margin-left: .5rem;
        padding: 0.1rem;        
        border: 1px solid #FAF4D3;
        box-shadow: none;
        border-radius: 50%;
        width: 2rem;
        background: #fff;
    }
}